* {
    margin: 0;
    padding: 0;
}

@font-face {
    font-family: 'H';
    src: url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Bold.eot);
    src: url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Bold.woff2) format("woff2"), url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Bold.woff) format("woff"), url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Bold.ttf) format("truetype");
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'H';
    src: url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Medium.eot);
    src: url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Medium.woff2) format("woff2"), url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Medium.woff) format("woff"), url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Medium.ttf) format("truetype");
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'H';
    src: url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Book.eot);
    src: url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Book.woff2) format("woff2"), url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Book.woff) format("woff"), url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Book.ttf) format("truetype");
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'H';
    src: url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Light.eot);
    src: url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Light.woff2) format("woff2"), url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Light.woff) format("woff"), url(//www.hostinger.com/tutorials/wp-content/themes/tutorialstheme/public/css/../fonts/H-Light.ttf) format("truetype");
    font-weight: 300;
    font-style: normal;
}

body {
    background-image: linear-gradient(109.6deg, rgba(53,65,140,1) 0%, rgba(92,169,196,1) 100%);
    color: white;
    width: 100%;
    height: 100%;

    font-size: 10px;
    font-family: 'H', 'Trebuchet MS', Helvetica;
    word-break: break-word;
    font-weight: 400;
    font-style: normal;
}
